/**
 * 
 * @param num
 */
export function tenThoursand(num?: number | string): string {
  if (!num) return "";

  try {
    const n = typeof num === "number" ? num : Number.parseInt(num);
    let str = n > 10000 ? (n / 10000).toFixed(0) + "" : n.toFixed(0);
    return str;
  } catch (error) {
    return num.toString();
  }
}

/**
 * 
 * @param date
 * @param fmt 
 */
export function dateFormat(date, fmt = "yyyy-MM-dd"): string {
  // 
  if (date === null || date === undefined || date === "") {
    return "";
  }

  // 
  if (typeof date === "number") {
    date = new Date(date);
  }

  let o = {
    "M+": date.getMonth() + 1, // 
    "d+": date.getDate(), // 
    "h+": date.getHours(), // 
    "m+": date.getMinutes(), // 
    "s+": date.getSeconds(), // 
    "q+": Math.floor((date.getMonth() + 3) / 3), // 
    S: date.getMilliseconds() // 
  };

  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );

  for (var k in o) {
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  }
  return fmt;
}
